// Headings colours
// ================================================


hr {
  border-top-width: 1px !important;
}

figure {
  border-radius: 0.2rem;
  position: relative;
  overflow: hidden;
  margin: 2rem auto;
  text-align: center;

  figcaption {
    font-size: 13px;
    text-align: center;
    margin-top: 10px;
    font-style: italic;
  }

  &.float-left,
  &.float-right {

    img {
      width: 100%;
    }
  }

  @include media-breakpoint-down(sm) {

    &.float-left,
    &.float-right {
      float: none !important;
      margin-left: 0;
      margin-right: 0;
    }
  }

  @include media-breakpoint-up(md) {
    &.float-left {
      margin-right: 32px;
      margin-top: 0.652rem;
      margin-bottom: 0.652rem;
      max-width: 50%;
    }

    &.float-right {
      margin-left: 32px;
      margin-top: 0.652rem;
      margin-bottom: 0.652rem;
      max-width: 50%;
    }
  }
}

.underlined {
  position: relative;
  display: inline-block;
  text-decoration: none;
  padding: 0 1px;
  transition: color ease 0.3s;

  &:before,
  &:after {
    content: '';
    position: absolute;
    background-color: #fff;
    z-index: 0;
    height: 1px;
  }

  &:before {
    width: 0%;
    left: 0;
    bottom: 0;
    transition: width ease 0.4s;
  }

  &:after {
    width: 100%;
    left: 0;
    bottom: 0;
    transition: all ease 0.6s;
  }

  &:hover {
    &:before {
      width: 100%;
    }

    &:after {
      left: 100%;
      width: 0%;
      transition: all ease 0.2s;
    }
  }
}

ul.list-buttons {
  list-style: none;
  padding-left: 0 !important;

  li {
    margin-bottom: 4px !important;

    &:last-child {
      margin-bottom: 0 !important;
    }
  }
}

ul.list-team {
  padding-left: 70px !important;
  list-style: none;

  li {
    margin-bottom: 25px !important;

    .initials {
      font-size: 16px;
      position: absolute;
      left: -70px;
      top: 50%;
      transform: translateY(-50%);
      background-color: $card-primary-bg;
      overflow: hidden;
      border-radius: 100%;
      width: 50px;
      height: 50px;
      color: #fff;
      text-align: center;
      line-height: 50px;
    }
  }

  a {
    display: inline-block;
    margin-bottom: 3px;
  }

  small {
    color: #606577;
  }
}

ul.list-bulleted {
  padding-left: 20px;
  list-style: none;

  li {
    position: relative;

    &:before {
      content: "\E974";
      font-family: "FabricMDL2Icons";
      position: absolute;
      top: 1px;
      left: -25px;
      font-size: 10px;
    }

    &:after {
      border: 1px solid;
      border-radius: 100%;
      content: "";
      position: absolute;
      top: 0;
      left: -30px;
      display: block;
      width: 20px;
      height: 20px;
    }
  }

  a {
    &:hover {
      text-decoration: underline;
    }
  }
}

.lead {
  @include media-breakpoint-down(sm) {
    font-size: 20px;
  }
}

a {

  &[href$=".pdf"],
  &[href$=".xls"],
  &[href$=".xlsx"],
  &[href$=".doc"],
  &[href$=".docx"],
  &[href$=".ppt"],
  &[href$=".pptx"] {
    padding-left: 30px;
  }

  &[href$=".pdf"]:before,
  &[href$=".xls"]:before,
  &[href$=".xlsx"]:before,
  &[href$=".doc"]:before,
  &[href$=".docx"]:before,
  &[href$=".ppt"]:before,
  &[href$=".pptx"]:before {
    content: "";
    font-family: "FabricMDL2Icons";
    font-size: 1.25rem;
    position: absolute;
    left: 0;
  }

  &[href$=".pdf"]:before {
    color: #d63644;
    content: "";
  }

  &[href$=".xls"]:before,
  &[href$=".xlsx"]:before {
    color: #39bfa0;
    content: "";
  }

  &[href$=".doc"]:before,
  &[href$=".docx"]:before {
    color: #065381;
    content: "";
  }

  &[href$=".ppt"]:before,
  &[href$=".pptx"]:before {
    color: #e99d29;
    content: "";
  }
}


.content {

  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  .h1,
  .h2,
  .h3,
  .h4,
  .h5,
  .h6 {
    font-family: $font-family-base;
  }

  // Links

  a {
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }
  }


  // Paragraphs
  // =================================================

  p {
    line-height: 26px;
    margin: 15px 0;
  }

  .lead {
    font-size: 26px;
    line-height: 42px;
    margin-bottom: 60px;
    font-weight: 400;
  }


  // Headings and spacings
  // =================================================
  h2+p,
  h3+p,
  h4+p,
  h5+p,
  h6+p,
  .h2+p,
  .h3+p,
  .h4+p,
  .h5+p,
  .h6+p {
    margin-top: 20px;
  }

  h2 {
    margin-top: 48px;
  }

  h3 {
    margin-top: 48px;
  }

  h4 {
    margin-top: 48px;
  }


  // Lists
  // =============================================

  ul,
  ol {
    margin: 15px 0;
    padding-left: 0;
    list-style: none;

    li {
      position: relative;
      margin-top: 12px;

      strong {
        font-weight: bolder;
      }
    }
  }


  blockquote {
    position: relative;
    padding: 70px 35px 40px 35px;
    background-color: #fff;
    font-size: 26px;
    line-height: 42px;
    letter-spacing: 0.025rem;
    color: $primary;
    margin: 30px 0;
    border-top-right-radius: 0.25rem;
    border-bottom-right-radius: 0.25rem;
    @include box-shadow(0px 3px 10px 0px rgba(0, 0, 0, 0.1));

    small {
      display: block;
      margin-top: 20px;
      color: #141624;
      font-size: 14px;

      &:before {
        content: "";
        width: 30px;
        border-top: 1px solid $primary;
        position: relative;
        display: inline-block;
        margin-right: 15px;
        top: -5px;
      }
    }

    &:before {
      content: "";
      width: 3px;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-color: $blue-emphasis;
    }

    &:after {
      content: "";
      position: absolute;
      font-family: theme-icons;
      color: $primary;
      font-size: 32px;
      top: 18px;
    }

    @include media-breakpoint-down(sm) {
      padding-left: 20px;
      padding-right: 20px;
    }

    @include media-breakpoint-up(lg) {

      &.pull-to-left {
        float: left;
        margin-right: 35px;
        max-width: 65%;
      }
    }

    @include media-breakpoint-up(xl) {
      &.pull-to-left {
        margin-left: -25%;
      }
    }

    @include media-breakpoint-up(xxl) {
      &.pull-to-left {
        margin-left: -30%;
        max-width: 70%;
      }
    }
  }



  // Embeds
  //

  .embed-responsive {
    margin: 1.75em 0;
  }
}


// Tables
// ===================================================

table,
.table {
  th {
    background-color: $table-head-bg;
    color: $table-head-color;
    text-transform: uppercase;
    font-size: 12px;
    line-height: 21px;
    letter-spacing: 0.05rem;
    padding-top: 1.25rem;
    padding-bottom: 1.25rem;
  }

  td {
    font-size: 0.875rem;

    strong {
      font-size: 0.75rem;
    }
  }
}

